// Views foundation
.view{
  @include full;
  background: $midnight;
  .view-content{
    background: #fff;
  }
  .view-footer{
    .button{
      font-size: 1rem;
      padding: .8rem 2.5rem;
      margin: .6rem;
      @include button;
    }
  }
}

// Photo teasers
.view-photos{
  .view-content{
    background: #000;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .views-row{
    position: relative;
    width: 33.333%;
  }
  .photo-teaser{
    position: relative;
    overflow: hidden;
    z-index: 10;
    &:focus,
    &:hover{
      .photo-title{
        bottom: 0;
      }
    }
  }
  .photo-title{
    position: absolute;
    left: 0;
    bottom: -4rem;
    width: 100%;
    padding: 1rem;
    font-size: 1.25vw;
    font-weight: 900;
    text-transform: uppercase;
    background: rgba(255,255,255,.8);
    transition: .2s all cubic-bezier(0.5, 0, 0, 1);
  }
}

// Article teasers
.view-articles{
  .article-teaser{
    display: -webkit-box; // There's a bug with display:flex;
  }
  .article-section{
    width: 50%;
    float: left;
    position: relative;
  }
  .article-image{
    &:focus,
    &:hover{
      ul.links a{
        opacity: 1;
      }
    }
  }
  ul.links{
    display: flex;
    a{
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 4vw;
      font-weight: 900;
      text-transform: uppercase;
      background: rgba(255,255,255,.8);
      transition: .2s all cubic-bezier(0.5, 0, 0, 1);
    }
  }
  .article-content{
    padding: 4vw;
  }
  .node-title{
    font-weight: 900;
    font-size: 2.5vw;
    text-transform: uppercase;
  }
  .field-name-body{
    p{
      font-size: 1.25vw;
    }
  }
  .field-name-field-tags{
    position: absolute;
    bottom: 4vw;
    left: 4vw;
    .field-item{
      display: inline;
    }
    a{
      text-transform: uppercase;
      font-size: 1.25vw;
      margin-right: 1vw;
    }
  }
}

// Pagers
.pager{
  float: right;
  width: auto;
  font-size: 1em;
  line-height: 1;
  text-transform: capitalize;
  background: $midnight;
  li{
    float: left;
    &.pager-current{
      background: $cyan;
      color: #fff;
    }
    a{
      display: block;
      padding: 1.5em;
      color: #fff;
      float: left;
      transition: all .2s cubic-bezier(0.5, 0, 0, 1);
      &:hover,
      &:active{
        border: 0;
        color: #fff;
        background: $crimson;
        transition: all 0s cubic-bezier(0.5, 0, 0, 1);
      }
    }
  }
  .pager-current{
    display: block;
    padding: 1.5em;
    float: left;
    transition: all .2s cubic-bezier(0.5, 0, 0, 1);
  }
}
.pager-next,
.pager-previous{
  display: none;
}
.pager-ellipsis{
  padding: 1.5em .98em;
  color: #fff;
}

// Begin responsive work
// --------------------------------------------------

@media #{$large}{
  // Photo teasers
  .view-photos{
    .views-row{
      width: 50%;
    }
    .photo-title{
      font-size: 1rem;
    }
  }
}

@media #{$medium}{
  // Photo teasers
  .view-photos{
    .views-row{
      width: 50%;
    }
    .photo-title{
      font-size: .8rem;
    }
  }
}

@media #{$small}{
  // Photo teasers
  .view-photos{
    .views-row{
      width: 100%;
    }
    .photo-title{
      font-size: 1rem;
    }
  }
}